import react from "react";
import { Carousel } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import Guvi from "../src/Images/Guvi.png";
import TCS from "../src/Images/CaptureTCS.PNG";
import TCSN from "../src/Images/CaptureTCSN.PNG";
import DARE from "../src/Images/CaptureDARE.PNG";
import DARET from "../src/Images/CaptureDareT.PNG";

const Certification = () => {
  return (
    <>
      <section id="Certification">
        <div className="my-5">
          <h1 className="text-center">My Certifications and Participation</h1>
        </div>
        <div className="container-fluid nav_bg">
          <div className="row">
            <div className="col-7 mx-auto">
              <div className="Certification row gy-4 text-center">
                <Carousel>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      height="500px"
                      width="500px"
                      src={TCS}
                      alt="First slide"
                    />
                    
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      height="500px"
                      width="500px"
                      src={TCSN}
                      alt="First slide"
                    />
                    
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      height="500px"
                      width="500px"
                      src={Guvi}
                      alt="Second slide"
                    />

                    
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      height="500px"
                      width="500px"
                      src={DARE}
                      alt="Third slide"
                    />

                    
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      height="500px"
                      width="500px"
                      src={DARET}
                      alt="Third slide"
                    />

                    
                  </Carousel.Item>
                </Carousel>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Certification;
